
.teacher .view-detail, .student .view-detail, .partner .view-detail {
  display: none;
}
.teacher .list .cover {
  float: left;
  width: 7rem;
  height: 7rem;
}
.teacher .list .cover .img {
    border-radius: 1rem;
}
.teacher .list .cont {
  padding-left: 0.5rem;
}
.teacher .list .cont .description {
    margin-top: 0.6rem;
    -webkit-line-clamp: 3;
    line-height: 1.6;
}



